    Tabchange(event) {

        this.translate.get('my-wallet-page.refreshing').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var refresh = this.translate.instant('my-wallet-page.refreshing');
            //=> 'Something else'



            if (this.walletview === 'balances') {

                let loading = this.loadingCtrl.create({
                    content: refresh
                });

                loading.present();

                setTimeout(() => {
                    this.finance1();
                    loading.dismiss();
                }, 5000);




            } else if (this.walletview === 'transactions') {

                let loading = this.loadingCtrl.create({
                    content: refresh
                });

                loading.present();

                setTimeout(() => {
                    this.getTransactions();
                    loading.dismiss();
                }, 5000);





            } else if (this.walletview === 'basket') {

                let loading = this.loadingCtrl.create({
                    content: refresh
                });

                loading.present();

                setTimeout(() => {
                    // this.showbasket();
                    loading.dismiss();
                }, 5000);



            }

        })


    }


    doRefresh(refresher) {
        console.log('Begin async operation', refresher);

        if (this.walletview === 'balances') {
            this.finance();
            setTimeout(() => {
                console.log('Async operation has ended');
                refresher.complete();
            }, 2000);

        } else if (this.walletview === 'transactions') {
            this.getTransactions();

            setTimeout(() => {
                console.log('Async operation has ended');
                refresher.complete();
            }, 2000);



        } else if (this.walletview === 'basket') {

            // this.showbasket();

            setTimeout(() => {
                console.log('Async operation has ended');
                refresher.complete();
            }, 2000);


        }
    }





