    getTransactions() {
        this.user.TransctionList().subscribe(
            data => {
                // console.log(data);
                let datas = JSON.parse(JSON.stringify(data));
                let data2 = JSON.parse(datas._body);

                this.transactions = data2.data;
                console.log(this.transactions.length);

            }, error => {
                console.log(error.JSON);
                setTimeout(() => {
                    this.navCtrl.setRoot('page-general_error');
                }, 1000);
            },
        )

    }

    // go to deposit page
    depositmoney() {
        if (this.id) {
            clearInterval(this.id);
        }

        console.log(this.itemzero);
        this.navCtrl.push('page-add-amount', { "data1": this.itemzero });

    }

    // go to withdraw page
    withdraw() {

        if (this.id) {
            clearInterval(this.id);
        }

        this.navCtrl.push('page-withdraw-to-bank');
    }





    transaction(item) {


        this.user.Invest(item.legalId, item.shares).subscribe(
            data => {

                let datas = JSON.parse(JSON.stringify(data));
                console.log(datas);
                let data2 = JSON.parse(datas._body);
                this.check = this.user.checkDataEmpty(data2.data);
                console.log(this.check);
                if (this.check) {
                    this.navCtrl.setRoot('page-general_error');

                } else {

                    if (data2.success) {
                        // console.log(data2.data.Orders);
                        this.finance1();

                        var storedNames = JSON.parse(localStorage.getItem("chekout"));

                        var data1 = storedNames.find(x => x.shares === item.shares);
                        storedNames.splice(data1, 1);
                        localStorage.setItem("chekout", JSON.stringify(storedNames));
                        this.basket = JSON.parse(localStorage.getItem("chekout"));
                        console.log(this.basket);
                        this.navCtrl.setRoot('page-make-deposit1');
                    } else {
                        this.translate.get('my-wallet-page.my_basket_tab_info.transaction_failed_toast').subscribe((translated: string) => {
                            // console.log(translated);
                            // //=> 'Hello world'

                            // You can call instant() here
                            var message = this.translate.instant('my-wallet-page.my_basket_tab_info.transaction_failed_toast');
                            //=> 'Something else'
                            let toast = this.toastCtrl.create({
                                message: message,
                                duration: 3000,
                                position: 'top'
                            });

                            toast.onDidDismiss(() => {
                                console.log('Dismissed toast');

                            });

                            toast.present();

                        })
                    }


                }


            },
            error => {
                console.log(error.JSON);
                setTimeout(() => {
                    this.navCtrl.setRoot('page-general_error');
                }, 1000);
            },
        )
    }


    // go to front list page

    Browse() {
        this.navCtrl.setRoot('page-home');
    }

    delete(item) {

        this.translate.get('my-wallet-page.my_basket_tab_info.message').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var message = this.translate.instant('my-wallet-page.my_basket_tab_info.message');
            var yes = this.translate.instant('my-wallet-page.my_basket_tab_info.alert_yes_btn');
            var no = this.translate.instant('my-wallet-page.my_basket_tab_info.alert_no_btn');
            let alert = this.alertCtrl.create({
                title: '',
                message: message,
                buttons: [
                    {
                        text: yes,
                        role: 'yes',
                        handler: () => {

                            var storedNames = JSON.parse(localStorage.getItem("chekout"));

                            var data1 = storedNames.find(x => x.shares === item.shares);
                            storedNames.splice(data1, 1);
                            localStorage.setItem("chekout", JSON.stringify(storedNames));
                            this.basket = JSON.parse(localStorage.getItem("chekout"));
                            console.log(this.basket);

                        }
                    },
                    {
                        text: no,
                        handler: () => {
                            console.log('no clicked');
                        }
                    }
                ]
            });
            alert.present();

        })


    }
